// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Sniglet", cursive;
  --content-width: 1170px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --dark-green: #286f6c;
  --dark-blue: #0d2f3f;
  --dark-grey: #323836;
  --yellow: #fda300;
  --white: #ffff;
  --orange: #ff525d;
  --blue: #34abe0;
  --light-green: #4bc999;
}
