.skills {
  padding: 50px 0;
  &__list {
    display: flex;
    justify-content: space-between;
    gap: 30px;
    @include tablet {
      flex-direction: column;
      align-items: center;
    }
  }

  &__item {
    flex: 0 1 33.33%;
    box-shadow: 0px 4px 8px rgb(0 0 0 / 12%);
    padding: 25px;
    border-radius: 20px;
    border: 1px solid #f1f1f1;
    &:hover {
      .skills__info--html {
        color: var(--orange);
      }
      .skills__info--css {
        color: var(--blue);
      }
      .skills__info--js {
        color: var(--light-green);
      }
    }
  }

  &__img {
    width: 64px;
    height: 64px;
    margin-bottom: 30px;
  }

  &__info {
    margin: 0;
    font-family: "Palanquin Dark", sans-serif;
    font-weight: 400;
    font-size: 24px;
    line-height: 36px;
    text-transform: uppercase;
    color: var(--dark-blue);
    transition: color 0.4s ease-out;
    margin-bottom: 20px;
  }

  &__descr {
    margin: 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 200%;
    color: var(--dark-blue);
    margin-bottom: auto;
  }
}
