.header {
  padding: 25px 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: var(--white);
  z-index: 60;
  @include small-tablet {
    padding: 15px 0;
  }
  &__container {
    position: relative;
    z-index: 50;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo {
    font-family: "Poppins";
    font-weight: 700;
    font-size: 32px;
    line-height: 100%;
    color: var(--dark-blue);
    position: relative;
    z-index: 6;
    margin-right: 20px;
    &::after {
      content: "";
      display: block;
      height: 8px;
      width: 8px;
      background-color: var(--orange);
      border-radius: 50%;
      position: absolute;
      bottom: 0;
      right: -10px;
    }
  }
}
