.hero {
  padding-top: 150px;
  @include small-tablet {
    padding-top: 110px;
  }
  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 35px;
    @include small-tablet {
      justify-content: center;
      flex-direction: column;
    }
  }

  &__content {
    max-width: 450px;
    @include small-tablet {
      max-width: none;
      text-align: center;
    }
  }

  &__title {
    margin: 0;
    font-family: "Palanquin Dark", sans-serif;
    font-weight: 700;
    font-size: 80px;
    color: var(--dark-blue);
    z-index: 3;
    margin-bottom: 20px;
  }

  &__descr {
    font-size: 25px;
    font-weight: 400;
    line-height: 45px;
    color: var(--dark-blue);
    z-index: 3;
    margin-bottom: 100px;
    @include small-tablet {
      margin-bottom: 50px;
    }
  }
  &__pos {
    color: var(--orange);
  }
  &__welcome {
    font-size: 30px;
    color: var(--orange);
  }

  &__img {
    img {
      width: 100%;
    }
  }
}
