.footer {
  padding: 25px 0;
  &__container {
    display: flex;
    justify-content: center;
  }
  &__content {
    font-weight: 400;
    font-size: 12px;
    line-height: 21px;
    color: var(--dark-green);
  }
  &__link {
    transition: 0.4s;
    color: var(--dark-green);
    &:hover {
      color: var(--orange);
    }
  }
}
