.works {
  padding: 50px 0;
  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 25px;
    @include small-tablet {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  &__item {
    overflow: hidden;
    border-radius: 4px;
    &:hover {
      .works__link {
        transform: scale(1.1);
      }
      .works__info {
        transform: translateY(-5px);
        color: var(--orange);
      }
    }
  }
  &__link {
    display: inline-block;
    overflow: hidden;
    width: 100%;
    height: 170px;
    transition: 0.5s;
    img {
      display: block;
      width: 100%;
      height: 100%;
      border-radius: 10px;
      box-shadow: 0px 4px 8px rgb(0 0 0 / 12%);
      transition: 0.5s;
    }
  }
  &__info {
    font-weight: 400;
    font-size: 18px;
    line-height: 27px;
    text-align: center;
    color: #070707;
    margin-top: 30px;
    position: relative;
    transition: 0.5s;
  }
}
