.nav {
  &__list {
    display: flex;
    gap: 35px;
    @include small-tablet {
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100%;
    }
  }
  &__link {
    font-size: 16px;
    line-height: 21px;
    position: relative;
    color: var(--dark-blue);
    transition: all 0.3s ease-in-out;
    text-transform: uppercase;
    &:hover {
      &::after {
        width: 100%;
      }
    }
    &::after {
      content: "";
      position: absolute;
      bottom: -5px;
      left: 0;
      width: 0;
      height: 2px;
      background-color: var(--yellow);
      transition: 0.4s;
    }
  }
  @include small-tablet {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--white);
    padding: 60px 0 30px 0;
    overflow: auto;
    transition: 0.4s;
    transform: translateY(-100%);
  }
  &.menu--active {
    transform: translateY(0);
    transition: 0.4s;
  }
}
.burger {
  display: none;
  @include burger;
  @include small-tablet {
    display: block;
  }
}
