.contacts {
  padding: 50px 0;
  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__text {
    text-align: center;
    font-weight: 400;
    font-size: 18px;
    line-height: 27px;
    margin: 0 auto 20px;
    color: var(--dark-blue);
    max-width: 285px;
  }
  &__link {
    text-align: center;
    display: inline-block;
    font-weight: 400;
    font-size: 18px;
    margin-bottom: 40px;
    color: #fff;
    padding: 8px 23px;
    background-color: var(--yellow);
    border-radius: 4px;
    transition: 0.4s;
    &:hover {
      background: var(--orange);
    }
  }
  &__list {
    display: flex;
    gap: 30px;
  }
  &__item {
    &:hover {
      .contacts__item-link {
        svg {
          transform: rotate(-30deg);
          fill: var(--orange);
        }
      }
    }
    &-link {
      display: inline-block;
      transition: 0.3s;
      svg {
        width: 40px;
        height: 40px;
        transition: 0.5s;
      }
    }
  }
}
